import Vue from 'vue'
import Vuex, { Store } from 'vuex'
import clone from '@/lib/clone';
import createId from '@/lib/createId';
import moment from 'moment';

Vue.use(Vuex)
// 
const initialRecordList = [
    {
        "tags": [{
            "id": "42",
            "name": "",
            "icon": "dress"
        }
        ],
        "notes": "",
        "type": "-",
        "amount": 200,
        "createdDate": moment("2020-07-01")
    }, {
        "tags": [{
            "id": "43",
            "name": "",
            "icon": "food"
        }
        ],
        "notes": "",
        "type": "-",
        "amount": 220,
        "createdDate": moment("2020-07-01")
    }, {
        "tags": [{
            "id": "45",
            "name": "",
            "icon": "traffic"
        }
        ],
        "notes": "",
        "type": "-",
        "amount": 5,
        "createdDate": moment("2020-07-01")
    }, {
        "tags": [{
            "id": "47",
            "name": "",
            "icon": "cash"
        }
        ],
        "notes": ".",
        "type": "-",
        "amount": 520,
        "createdDate": moment("2020-05-20")
    }
];

const store = new Vuex.Store({
    state: {
        tagList: [] as Tag[],
        errorMessage: '',
        recordList: [] as RecordItem[],
        currentTag: undefined as Tag | undefined
    },
    getters: {

    },
    mutations: {
        //Record
        fetchRecord(state) {
            state.recordList = JSON.parse(localStorage.getItem("recordList") || "[]") as RecordItem[];
            if (state.recordList.length === 0) {
                state.recordList = initialRecordList;
            }
            return this.recordList;
        },
        createRecord(state, record) {
            const copyRecord = clone(record);
            copyRecord.createdDate = new Date().toString();
            state.recordList.push(copyRecord);
            store.commit("saveRecord");
        },
        saveRecord(state) {
            window.localStorage.setItem("recordList", JSON.stringify(state.recordList));
        },
        //Tag
        fetchTag(state) {
            state.tagList = JSON.parse(window.localStorage.getItem("tagList") || '[]');
            // 
            if (state.tagList.length === 0) {
                store.commit("createTag", { name: "", icon: "dress" });
                store.commit("createTag", { name: "", icon: "food" });
                store.commit("createTag", { name: "", icon: "hotel" });
                store.commit("createTag", { name: "", icon: "traffic" });
                store.commit("createTag", { name: "", icon: "travel" });
                store.commit("createTag", { name: "", icon: "cash" });
                store.commit("createTag", { name: "", icon: "allowance" });
                store.commit("createTag", { name: "", icon: "sock" });
                store.commit("createTag", { name: "", icon: "message" });
                store.commit("createTag", { name: "", icon: "social-contact" });
                store.commit("createTag", { name: "", icon: "salary" });
                store.commit("createTag", { name: "", icon: "bonus" });
                store.commit("createTag", { name: "", icon: "part-time" });
            }
            return state.tagList;
        },
        createTag(state, { name, icon = "default" }) {
            const names = state.tagList.map(item => item.name);
            if (names.indexOf(name) >= 0) {
                return state.errorMessage = '';
            }
            const id = createId().toString();
            state.tagList.push({ id, name, icon });
            store.commit("saveTag");
        },
        updateTag(state, payload: { id: string, name: string }) {
            const { id, name } = payload;
            const idList = state.tagList.map(item => item.id);
            if (idList.indexOf(id) >= 0) {
                const names = state.tagList.map(item => item.name);
                if (names.indexOf(name) >= 0) {
                    return state.errorMessage = '';
                } else {
                    const tag = state.tagList.find(item => item.id === id) as Tag;
                    tag.name = name;
                    store.commit("saveTag");
                }
            } else {
                state.errorMessage = '';
            }
        },
        saveTag(state) {
            window.localStorage.setItem("tagList", JSON.stringify(state.tagList));
        },
        removeTag(state, id) {
            const index = state.tagList.findIndex(v => v.id === id);
            if (state.tagList.splice(index, 1).length > 0) {
                state.currentTag = undefined;
            }
            store.commit("saveTag");
        },
        findTag(state, id) {
            state.currentTag = state.tagList.find(t => t.id === id);
            console.log('currentTag', state.currentTag);
        }

    }
})
export default store;